.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 10px; */
}
.container{
  /* width: 70vw; */
  /* height: 90vh; */
  background-color: rgb(218, 180, 250);
  border-radius: 5px;
  border: 2px solid bisque;
  box-shadow: 10px 10px 10px blanchedalmond;
}
.container .form-todo{
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container .form-todo .btn {
  width: 15%;
  height: 38px;
  border: 1px solid #0b0b0b;
  border-radius: 32px;
  padding: -6px;
  background: transparent;
  font-size: 14px;
  color: #000b63;
  cursor: pointer;
}
.container .form-todo .btn:hover{
 background-color: rgb(144, 222, 236);
 color: rgb(114, 126, 122);
}

.todo_wrapper{
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  margin-top: 20px;
}

.to_title{
  font-size: 20px;
  font-weight: 700;
  color: #373737;
}
.todo_list{
  width: 31%;
  height: 60vh;
  display: fl;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}
.to_card{
  width: 100%;
  height: auto;
  color: rgb(28, 28, 28);
  background-color: rgb(18, 238, 212);
  padding: 2px 2px;
  border-radius: 5px;
  margin-top: 3px;
  margin-right: 6px;
}
.to_card .card-tex{
  margin: auto;
 
}
.icon-check-todo {
  background-color: rgb(210 248 235);
  color: rgb(185, 185, 185);
  padding: 4px;
  font-size: 21px;
  border-radius: 11px;
  cursor: pointer;
}
.icon-trash-todo {
  background-color: rgb(238, 86, 40);
  color: rgb(216, 216, 216);
  padding: 5px;
  font-size: 21px;
  border-radius: 11px;
  cursor: pointer;
}
.progress{
  width: 100%;
  height: auto;
  color: rgb(28, 28, 28);
  background-color: rgb(18, 238, 212);
  padding: 2px 2px;
  border-radius: 5px;
  margin-top: 3px;
  margin-right: 6px;
}
.icon-progress-todo{
background-color: #e4e3de;
color: rgb(8, 226, 8);
padding: 4px;
font-size: 21px;
border-radius: 11px;
cursor: pointer;
/* margin-top: 5px; */
}
