  margin: auto;
 
} */
.completed{
  width: 100%;
  height: auto;
  color: rgb(28, 28, 28);
  background-color: rgb(156, 152, 216);
  padding: 5px 2px;
  border-radius: 5px;
  margin-top: 3px;
  margin-right: 6px;
}
.completed p{
  padding-top: 3px;
}
.s{
  background-color: rgb(87, 238, 5);
}